import { ColumnDef, Row } from '@tanstack/react-table';
import adel from '@/assets/imgs/user.png';
import { Employee } from '@/types/employee.types';
import ViewIcon from '@/components/icons/ViewIcon';
import { Link } from 'react-router-dom';
import { EmployeeSalary } from '../../types/employeeSalary.types';

// Create a separate component for the actions cell
const ActionsCell = ({ row }: { row: Row<EmployeeSalary> }) => {
    return (
        <div className="cursor-pointer flex justify-center">
            <Link to="/human-resources/salaries-details/add-allowance">
                <ViewIcon className="text-primery-blue-400" />
            </Link>
        </div>
    );
};

export const columns: ColumnDef<EmployeeSalary>[] = [
    {
        accessorKey: 'id',
        header: () => {
            return (
                <div className="text-sm text-nowrap px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    اسم الموظف
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="flex flex-row justify-start items-center gap-4 text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                <img src={adel} alt="" className="rounded-full w-10 h-10 object-cover" />
                <div className="flex flex-col gap-1">
                    <p className="tracking-normal text-primery-blue-900 font-medium text-sm">{row.original.name}</p>
                    <p className="tracking-normal text-white-blue-800 font-normal text-sm">{row.original.email}</p>
                </div>
            </div>
        ),
    },
    {
        accessorKey: 'name',
        header: () => {
            return (
                <div className="text-sm text-nowrap px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    عناصر الراتب
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                {/* {row.original.employee_number} */}
            </div>
        ),
    },
    {
        accessorKey: 'status',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    ر.س
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original.net_salary}
                </div>
            );
        },
    },
    {
        id: 'actions',
        // header: () => {
        //     return (
        //         <div className="text-sm text-nowrap text-center px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
        //             للاطلاع على الرسائل
        //         </div>
        //     );
        // },
        cell: ({ row }) => <ActionsCell row={row} />,
    },
];
